body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.head{
  width: auto;
  height: 10vh;
  padding-top: 5vh;
  background-color: beige;
  display: flex;
  align-items: center;
  justify-content: center;
  color:rgb(163, 163, 119);
  text-shadow: 5px 15px 30px rgb(156, 156, 142);
  font-size: 1.3em;
}
.search{
  width: auto;
  height: 15vh;
  background-color: beige;
  display: flex;
  align-items: center;
  justify-content: center;
}
.input{
  width: 30%;
  height: 5vh;
  font-size: 1.5em;
  box-shadow: 5px 15px 30px rgb(189, 189, 174);
  border: none;
  background-color: rgb(252, 252, 239);
  border-radius: 5px;
  margin: 5%;
  outline: none;
}
.button{
  width: 15%;
  height: 5vh;
  background-color: rgb(224, 224, 196);
  box-shadow: 5px 15px 30px rgb(189, 189, 174);
  border: none;
  border-radius: 5px;
  outline: none;  
}
.bg_color{
  background-color: beige;
}
h2{
  width: auto;
  height: 10vh;
  background-color: beige;
  display: flex;
  align-items: center;
  justify-content: center;
  color:rgb(163, 163, 119);
  text-shadow: 5px 15px 30px rgb(156, 156, 142);
  font-size: 1em;
}
.recipe{
  width: auto;
  height: auto;
  background-color: rgb(253, 253, 241);
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 2vh;
}
.button1{
  width: 20%;
  height: 5vh;
  margin: 1vh;
  background-color: rgb(224, 224, 196);
  box-shadow: 5px 15px 30px rgb(189, 189, 174);
  border: none;
  border-radius: 5px;
  outline: none;
}
a{
  color:black;
  outline: none;
  text-decoration: none;
}
.box{
  width: 60vh;
  height: auto;
  border: 2px black;
  background-color: rgb(215, 215, 223);
  margin: 3vh;
  display: grid;
  justify-items: center;
  align-items: center;
  box-shadow: 5px 15px 30px rgb(189, 189, 174);
  border-radius: 10px;
}
.button:hover{
  background-color: rgb(189, 189, 126);
}
.button1:hover{
  background-color: rgb(131, 131, 83);
}

@media only screen and (max-width:640px){
  .recipe{
    width: auto;
    height: auto;
    background-color: rgb(253, 253, 241);
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 10vh;
  }
  .box{
    width: 100vh;
    height: auto;
    border: 2px black;
    background-color: rgb(215, 215, 223);
    margin: 3vh;
    display: grid;
    justify-items: center;
    align-items: center;
    box-shadow: 5px 15px 30px rgb(189, 189, 174);
    border-radius: 10px;
  }
  .head{
    padding: 5vh;
    text-shadow: 5px 15px 30px rgb(156, 156, 142);
  }
  .input{
    width: 60%;
    height: 50%;
    box-shadow: 5px 15px 30px rgb(189, 189, 174);
  }
  .search{
    padding: 5vh;
  }
  .button{
    width: 20%;
    height: 50%;
    box-shadow: 5px 15px 30px rgb(189, 189, 174);
  }
  .button1{
    width: 30%;
    height: 70%;
    box-shadow: 5px 15px 30px rgb(189, 189, 174);
  }
}
@media only screen and (max-width:360px){
.head{
  text-align: center;
  font-size: 1em;
  text-shadow: 5px 15px 30px rgb(156, 156, 142);
}
.input{
  width: 50%;
  box-shadow: 5px 15px 30px rgb(189, 189, 174);
}
.button{
  width: 20%;
  box-shadow: 5px 15px 30px rgb(189, 189, 174);
}
}